.intro{display:grid;grid-template-columns:repeat(2, 1fr);position:relative}@media all and (max-width: 414px){.intro{grid-template-columns:180px 180px}}@media all and (max-width: 375px){.intro{grid-template-columns:165px 165px}}@media all and (max-width: 320px){.intro{grid-template-columns:140px 140px}}.intro__name{grid-column:1/2}@media all and (max-width: 768px){.intro__name{grid-column:1/2}}.intro__name .let{font-size:1.75rem;color:#a96fe3}.intro__name .let--dark{color:#a96fe3}.intro__name h1{font-size:3.5rem;font-weight:bold;line-height:1}@media all and (max-width: 768px){.intro__name h1{font-size:3rem}}@media all and (max-width: 414px){.intro__name h1{font-size:2rem}}.intro__name p{padding-top:.5rem}.intro__info{display:flex;flex-direction:row;align-items:center;margin-top:1rem}.intro__info h3{font-weight:bold;font-size:1.5rem}.intro__info p{font-size:1.2rem;padding-left:1rem}.intro__info div{margin-right:2rem;display:flex;flex-direction:row;align-items:center}.intro .avatar{width:13rem;height:13rem;border-radius:50%;background-size:cover;background-repeat:no-repeat;background-position:0 20%;grid-row:1/3;grid-column:2/3;justify-self:center;box-shadow:0 0 10px gray;transition:.3s}@media all and (max-width: 768px){.intro .avatar{grid-column:2/3;grid-row:1/3;justify-self:end;width:10rem;height:10rem}}@media all and (max-width: 414px){.intro .avatar{width:7rem;height:7rem;justify-self:end}}.intro .avatar--dark{box-shadow:none}.main-info{margin-top:1.5rem;padding-right:5rem}@media all and (max-width: 1024px){.main-info{padding-right:0}}.main-info .technology-text{font-size:1.25rem;padding-right:1rem;font-weight:400}.main-info .cursor{opacity:1;-webkit-animation:cursor 1s ease-in-out infinite;animation:cursor 1s ease-in-out infinite}@-webkit-keyframes cursor{0%{opacity:1}50%{opacity:0}100%{opacity:1}}@keyframes cursor{0%{opacity:1}50%{opacity:0}100%{opacity:1}}.main-info span{display:inline-block;line-height:1;margin:1rem 0 0 1rem;font-size:3.5rem;font-weight:bold}@media all and (max-width: 414px){.main-info span{font-size:2rem}}.main-info__item{display:grid;grid-template-columns:1fr 4fr;margin-top:2rem}@media all and (max-width: 1024px){.main-info__item{grid-template-columns:1fr}}.main-info__item .bracket{font-weight:400;display:inline-block;margin:0;line-height:1;font-size:2.5rem;transform:translateY(-5%);color:#330ccf}.main-info__item .bracket--dark{color:#8fffab}.main-info h3{font-size:1.5rem;font-weight:bold;line-height:1.15}.main-info p{margin-top:auto;margin-left:1rem}@media all and (max-width: 1024px){.main-info p{padding-top:1rem}}@media all and (max-width: 414px){.main-info p{margin-left:0}}@media all and (max-width: 414px){.main-info{padding-right:0}}.technologies .technology{height:2rem;margin:.5rem 1rem 0 0}.technologies .technology--dark{-webkit-filter:invert(1);filter:invert(1)}.portfolio{background:#FFFFFF;box-shadow:0 0 10px rgba(0,0,0,0.25);border-radius:10px;padding:1.5rem 2rem;display:flex;flex-direction:column;justify-content:space-between;max-height:95vh;overflow:hidden;overflow-y:scroll}.portfolio .description{grid-column:1/3}.portfolio .description__stack{color:white}.portfolio .description p{padding:.5rem 0}.portfolio .description a{color:#a96fe3}@media all and (max-width: 1024px){.portfolio{margin-top:1.5rem}}@media all and (max-width: 500px){.portfolio{padding:1rem 1rem}}.portfolio--dark{background-color:black;box-shadow:0 0 10px rgba(255,255,255,0.25)}.portfolio__projects{display:grid;grid-template-columns:repeat(2, minmax(100px, 15vw));grid-auto-rows:minmax(8vw, auto);grid-gap:1rem;justify-content:center}@media all and (max-width: 1024px){.portfolio__projects{justify-content:start;grid-template-columns:1fr;margin-bottom:1rem}}@media all and (max-width: 414px){.portfolio__projects{grid-auto-rows:minmax(8vw, auto)}}.portfolio__projects__item{width:100%;text-align:center;font-size:1.5rem;font-weight:bold;color:white;position:relative;border-radius:10px;grid-column:1/3}@media all and (max-width: 414px){.portfolio__projects__item{font-size:1rem}}@media all and (max-width: 768px){.portfolio__projects__item{font-size:1.25rem}}.portfolio__projects__item.palettor{background:linear-gradient(180deg, #5EE2FF 0%, #C63939 52.6%, #660C7C 100%)}.portfolio__projects__item.palettor div{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.portfolio__projects__item.tasker{color:#282c34;background-size:cover;background:white no-repeat center}.portfolio__projects__item.tasker div{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.portfolio__projects__item.analyzer{color:black;background:white no-repeat bottom}.portfolio__projects__item.analyzer div{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.portfolio__projects__item.bermudy{color:white;background-size:cover;background:white no-repeat center}.portfolio__projects__item.bermudy div{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.contacts{display:grid;grid-template-columns:auto auto}.contacts__social__item{height:2rem;margin:.5rem .5rem 0 0;transition:.3s}.contacts__social__item--dark{-webkit-filter:invert(1);filter:invert(1)}.contacts__phone{grid-column:1/2;margin-top:1rem}.settings{grid-column:2/3;grid-row:2/3;margin-top:2rem;justify-self:end;margin-bottom:1rem}.settings #dark-check{opacity:0}.settings #dark-check:checked ~ .dark-mode>.toggle{left:100%;transform:translateX(-108%)}.settings .dark-mode{display:inline-block;position:relative;height:2rem;width:3.5rem;border-radius:1rem;box-shadow:0 0 5px gray}.settings .dark-mode .toggle{position:absolute;height:1.7rem;width:1.7rem;background:#61dafb no-repeat center;background-size:70%;border-radius:.85rem;left:3px;top:2.5px;transition:.3s}.settings .dark-mode .toggle:hover{cursor:pointer}.settings .lang{height:2rem;justify-self:end;transition:.3s}.settings .lang:hover{cursor:pointer}.settings .lang--dark{-webkit-filter:invert(1);filter:invert(1)}::-webkit-scrollbar{width:3px;height:3px}::-webkit-scrollbar-butto